html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: auto;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
}

.navigation {
  text-transform: uppercase;
  height: 60px;
}

.navigation__item {
  text-wrap: nowrap;
  margin: 0 20px 0 0;
  position: relative;
}

.navigation__item:last-child {
  margin-right: 0;
}

.navigation__list {
  margin: 0;
  padding-inline-start: 0;
  list-style-type: none;
  display: flex;
}

.navigation__link {
  cursor: default;
  color: #060b35;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.navigation__link:hover, .navigation__link--is-active {
  color: #00acdc;
}

.navigation__link--is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  top: 56px;
}

.container {
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media screen and (width <= 487px) {
  .container {
    grid-template-columns: repeat(1, 200px);
  }
}

@media screen and (width >= 488px) and (width <= 767px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width >= 768px) and (width <= 1023px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

@keyframes bigger-size {
  to {
    transform: scale(1.2);
  }
}

.card {
  box-sizing: border-box;
  background: url("imac.202c52ff.jpeg") 50% 32px / 160px 134px no-repeat;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding-left: 16px;
  padding-right: 16px;
}

.card__name {
  color: #060b35;
  margin-top: 206px;
  margin-bottom: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 4px 0 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-top: 15.5px;
  display: flex;
}

.card__review-txt {
  color: #060b35;
  text-align: right;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price-txt {
  color: #616070;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-amount {
  color: #060b35;
  text-align: right;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-link {
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin-top: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button-link:hover {
  box-sizing: border-box;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.card:hover {
  animation: .2s linear .1s forwards bigger-size;
}

.card:hover .card__name {
  cursor: pointer;
  color: #00acdc;
}

.stars {
  justify-content: space-between;
  width: 96px;
  display: flex;
}

.stars__star {
  content: "";
  background-image: url("star.822d561e.svg");
  width: 12px;
  height: 12px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
